.chatboxLayout {
    padding: 2rem 7rem;
    overflow-y: scroll;
    flex: 1;
    
    display: flex;
    flex-direction: column;

    .messageBox {

        .message {
            font-size: 1.5rem;
            color: #444;
            margin-right: 5rem;
            flex: 1;
        }
        .date {
            font-size: 1.05rem;
            align-self: flex-end;
            margin-bottom: -3px;
            position: absolute;
            right: 1.1rem;
            bottom: .6rem;
            display: flex;
            align-items: center;

            i {
                margin-left: .6rem;
            }
        }
        i {
            color: transparent;
            cursor: pointer;
            transition: .15s ease-out;
        }

        &:hover i{
            color: #adadad;
        }
        &:hover p i{
            opacity: 0;
            visibility: hidden;
        }
    }

    .imageWrapper {
        .image {
            img {
                width: 100%;
                border-radius: 5px;
                cursor: pointer;
                object-fit: cover;
            }
        }
        .images {
            display: grid;
            grid-template-columns: auto auto;
            grid-template-rows: 17rem 17rem;
            grid-gap: 3px;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                cursor: pointer;
                border-radius: 4px;
            }
        }

        .message {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: .2rem .4rem;
            .text {
                font-size: 1.45rem;
                color: #444;
                margin-right: 1rem;
            }
            .date {
                font-size: 1.1rem;
            }
        }

        i {
            position: absolute;
            right: 8px;
            top: 5px;
            color: transparent;
            cursor: pointer;
            border-radius: 50%;
        }

        &:hover i {
            color: white;
            box-shadow: 0 8px 40px #666;
        }
    }

    @media (max-width: 1024px) {
        padding: 1.5rem 4rem;
    }

    @media (max-width: 600px) {
        padding: 1rem;
    }
}